<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="70%"
    direction="btt"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar>
      <div style="margin: 0 10px;">
        <el-row>
          <el-descriptions :border="true" direction="vertical" :column="7">
            <el-descriptions-item label="物料编号">{{ dialogForm.materialCode }}</el-descriptions-item>
            <el-descriptions-item label="物料名称">{{ dialogForm.materialName }}</el-descriptions-item>
            <el-descriptions-item label="规格型号">{{ dialogForm.materialSpec }}</el-descriptions-item>
            <el-descriptions-item label="评估类型">{{ dialogForm.materialAssessType_dictText }}</el-descriptions-item>
          </el-descriptions>
        </el-row>
        <div style="margin-top: 20px;">
          <el-descriptions :column="2">
            <el-descriptions-item label="条码数量">{{ form.inventoryList?form.inventoryList.length:0 }}</el-descriptions-item>
          </el-descriptions>
        </div>
        <el-row>
          <table-com
            :key="refreshKey"
            ref="baseTableRef"
            :loading="loading"
            :index="true"
            :selection="false"
            :table-data="form.inventoryList"
            :column-list="columnList"
            :table-height-type="310"
            :show-operate="false"
            :expand-list="expandList"
            :table-column-sort="false"
            @rowClick="handleRowClick"
            @refresh="searchInit"
          />
        </el-row>
        <!-- <pagination
          :page.sync="pageInfo.current"
          :limit.sync="pageInfo.size"
          :total="total"
          @pagination="init"
        /> -->
      </div>
    </el-scrollbar>
    <!-- <div class="drawer-footer">
      <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div> -->
    <!-- 树信息选择 -->
    <info-select
      v-model="selectedInfo.visible"
      :multiple="false"
      :extra-info="{}"
      @confirm="onSelectConfirm"
    />
  </el-drawer>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import InfoSelect from '@/components/ModalSelect/WarehouseLocation'
import { getDetailApi as getListApi, delDetailListApi as getDeleteApi } from '@/apis/modules/out-bound/outbound-information-detail'

const INIT_FORM = {
  code: '',
  supplierId: '',
  supplierName: '',
  contact: '',
  phone: '',
  purchaseDate: '',
  inventoryList: []
}

export default {
  name: 'InStorageInfoReceiptCom',
  components: {
    InfoSelect
  },
  mixins: [paginationPage],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM }
      }
    },
    stockId: {
      type: [Number, String],
      default: ''
    }
  },
  data() {
    return {
      title: '详情',
      refreshKey: new Date().getTime(),
      form: INIT_FORM,
      loading: false,
      rules: {},
      query: {},
      columnList: [
        {
          module: 'word',
          prop: 'barcodeNumber',
          label: '条码号',
          align: 'center',
          width: 200,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'num',
          label: '数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'currentPosition',
          label: '位置',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inWarehouseDate',
          label: '入库日期',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inWarehouseDays',
          label: '在库时长(天)',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      expandList: [],
      dialogVisible: false,
      dialogInfo: {},
      editInfoType: 'add',
      selectedInfo: {
        list: [],
        visible: false,
        radio: {},
        rowIndex: null
      }
    }
  },
  computed: {
    dialogTitle() {
      return this.editType === 'add' ? `新增${this.title}` : `编辑${this.title}`
    }
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM }
        } else {
          this.form = { ...value }
        }
      },
      immediate: true
    }
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          ...this.query,
          id: this.dialogForm.id
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.form.inventoryList = data.inventoryList.map(i => {
              return {
                ...i,
                isDel: false
              }
            })
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleConfirm(formName) {
      this.beforeClose()
    },
    // 删除
    handleDelete(index, item) {
      console.log('---操作---', index)
      this.$confirm('此操作将删除选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        getDeleteApi({
          id: item.id
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '删除成功'
            })
            this.$set(this.form.inventoryList, index, {
              ...this.form.inventoryList[index],
              storageId: '',
              storageName: '',
              isDel: !item.isDel
            })
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    // 恢复
    handleRestore(index, item) {
      this.$confirm('此操作将恢复选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        this.$set(this.form.inventoryList, index, {
          ...this.form.inventoryList[index],
          isDel: !item.isDel
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    onSelectConfirm(list) {
      if (list.length) {
        console.log(list)
        this.$set(this.form.inventoryList, this.selectedInfo.rowIndex, {
          ...this.form.inventoryList[this.selectedInfo.rowIndex],
          storageId: list[0].id,
          storageName: list[0].name
        })
      }
    },
    handleAdd() {
      this.editInfoType = 'add'
      this.dialogInfo = {}
      this.dialogVisible = true
    },
    handleModify() {
      this.dialogInfo = { ...this.form }
      this.editInfoType = 'modify'
      this.dialogVisible = true
    },
    handleRowClick({ row, rowIndex, column, cell, event }) {
      console.log(row, column, event)
      if (column.label === '操作') {
        return false
      }
      this.selectedInfo.rowIndex = rowIndex
      this.rightObj = { ...row }
    },
    refresh() {
      this.init()
    },
    initSelect() {
      this.init()
    },
    beforeClose(done) {
      // this.$refs.form.resetFields()
      this.$emit('input', false)
      this.form = Object.assign({}, { ...INIT_FORM })
      typeof done === 'function' && done()
    }
  }
}
</script>

  <style lang='scss' scoped>
  .container{
  width: 860px;
  margin: 0 auto;
  .left{
    width: 640px;
  }
  .right{
    margin-left: 20px;
    width: 180px;
  }
  }
  ::v-deep:focus {
    outline: 0;
  }
  ::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
  }

  ::v-deep .el-drawer__header {
    margin-bottom: 10px !important;
  }

  .drawer-footer{
    text-align: right;
  }
  </style>

