<template>
  <div class="app-container">
    <el-row class="clearfix">
      <div class="fl">
        <el-button @click="beforeClose">返回</el-button>
      </div>
      <div class="fr">
        <el-button type="primary" @click="handlePrintTemplate">打印预览</el-button>
      </div>
    </el-row>
    <el-row>
      <el-collapse accordion>
        <el-collapse-item title="单据信息" name="1">
          <el-descriptions :column="2">
            <el-descriptions-item label="凭证日期">{{ form.documentDate || '---' }}</el-descriptions-item>
            <el-descriptions-item label="记账日期">{{ form.accountingDate || '---' }}</el-descriptions-item>
            <el-descriptions-item label="出货工厂">{{ form.shipmentFactoryName || '---' }}</el-descriptions-item>
            <el-descriptions-item label="单据编号">{{ form.billNumber || '---' }}</el-descriptions-item>
            <el-descriptions-item label="出货仓库">{{ form.shipmentWarehouseName || '---' }}</el-descriptions-item>
            <el-descriptions-item label="移动类型">{{ form.movementType || '---' }}</el-descriptions-item>
            <el-descriptions-item label="成本中心">{{ form.costCenterName || '---' }}</el-descriptions-item>
            <el-descriptions-item label="总账科目">{{ form.ledgerSubjects || '---' }}</el-descriptions-item>
            <el-descriptions-item label="资产">{{ form.asset || '---' }}</el-descriptions-item>
            <el-descriptions-item label="收货工厂">{{ form.receiptFactoryName || '---' }}</el-descriptions-item>
            <el-descriptions-item label="订单">{{ form.orderNumber || '---' }}</el-descriptions-item>
            <el-descriptions-item label="收货仓库">{{ form.receiptWarehouseName || '---' }}</el-descriptions-item>
            <el-descriptions-item label="项目">{{ form.project || '---' }}</el-descriptions-item>
            <el-descriptions-item :label="form.billType_dictText || '---'">{{ form.affiliatedBillNumber || '---' }}</el-descriptions-item>
            <el-descriptions-item label="网络">{{ form.network || '---' }}</el-descriptions-item>
            <el-descriptions-item label="客户号">{{ form.custumerCode || '---' }}</el-descriptions-item>
            <el-descriptions-item label="转储单">{{ form.transferOrderNumber || '---' }}</el-descriptions-item>
          </el-descriptions>
        </el-collapse-item>
      </el-collapse>
    </el-row>
    <el-form ref="filterItem" :model="{}" inline label-width="100px" class="clearfix" @submit.native.prevent>
      <el-form-item prop="type" label="单据类型：">
        <span>{{ pullListMap[form.type] || '---' }}</span>
      </el-form-item>
      <el-form-item prop="explanation" label="说明：">
        <span>{{ form.explanation || '---' }}</span>
      </el-form-item>
      <el-form-item prop="stockOutNum" label="数量：" class="fr">
        <span>{{ form.stockOutNum }}</span>
      </el-form-item>
      <el-form-item prop="stockOutItems" label="出库项：" class="fr">
        <span>{{ form.stockOutItems }}</span>
      </el-form-item>
    </el-form>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="120"
        :table-column-sort="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="primary" :disabled="!form.id" @click="handleModify(scope.row)">详情</el-button>
        </template>
      </table-com>
    </el-row>
    <!-- <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" /> -->

    <!-- 详情 -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :stock-id="form.id"
      @confirm="init"
    />

    <print-templates v-model="dlgVisible" title="打印模板" @confirm="handleTemConfirm" />

  </div>
</template>

<script>
import tags from '@/mixins/tags'
import paginationPage from '@/mixins/paginationPage'
import EditDialog from './Dialog'
import PrintTemplates from './PrintTemplates'
import { getInfoApi, getSaveDetailApi as getListApi, printApi } from '@/apis/modules/out-bound/outbound-information-detail'

export default {
  name: 'InStorageInfoReceipt',
  components: {
    EditDialog,
    PrintTemplates
  },
  mixins: [tags, paginationPage],
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      query: {},
      form: {
        inWarehouseNumber: ''
      },
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'materialCode',
          label: '物料编码',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '物料名称',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialSpec',
          label: '规格型号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '评估类型',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'num',
          label: '数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnSets: [],
      dialogForm: {},
      pullListMap: {
        '1': '常规',
        '2': '战略'
      },
      pullList: [
        {
          code: '1',
          name: '常规'
        },
        {
          code: '2',
          name: '战略'
        }
      ],
      editType: 'add',
      dialogVisible: false,
      dlgVisible: false
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    },
    routeQuery() {
      // Tip 领料单号 单据类型
      const { billNumber, billType, sourcePage = null } = this.$route.query
      return { billNumber, billType, sourcePage }
    },
    routeParams() {
      // Tip 出库单号
      const { billNumber } = this.$route.params
      return { billNumber }
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.loading = true
      const params = {
        billNumber: this.routeParams.billNumber,
        filterOutInventoryNum: true
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data: { detailList, ...info }} = res
          if (!code) {
            this.form = { ...info }
            this.tableData = detailList || []
            if (this.form.billNumber) {
              // 修改当前tags
              this.setTagsTitleMixins(null, this.form.billNumber)
            }
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    initInfo() {
      this.loading = true
      const params = {
        deliveryReceiptId: this.routeParams.orderId
      }
      return new Promise((resolve, reject) => {
        getInfoApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.form = { ...data }
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    handleModify(item) {
      this.dialogForm = { ...item }
      this.editType = 'modify'
      this.dialogVisible = true
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    beforeClose() {
      this.$store.dispatch('tagsView/delView', this.$route)
      if (!this.routeQuery.sourcePage) {
        return this.$router.go(-1)
      }
      // Tip 页面关闭返回源页面(跳转前页面)
      this.$router.push({
        name: this.routeQuery.sourcePage,
        ...this.routeQuery.sourcePage === 'OutBoundInfoDetail' && {
          params: {
            billNumber: this.routeQuery.billNumber, // 单号
            billType: this.routeQuery.billType // 单据类型
          }
        }
      })
    },
    // Tip 打印
    handlePrint(item, printStyle) {
      const { billNumber } = item
      const params = {
        billNumber,
        ...printStyle && { printStyle }
      }
      return new Promise((resolve, reject) => {
        printApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            console.log('data', data)
            window.open(process.env.VUE_APP_BASE_API_WMS + data)
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 模板选择确认
    handleTemConfirm(flag) {
      this.handlePrint(this.form, flag)
        .then(res => {
          this.dlgVisible = false
        })
        .catch(err => {
          console.log('err', err)
        })
    },
    // 打印
    handlePrintTemplate() {
      this.handlePrint(this.form)
      // this.form.type === 2 && this.handlePrint(this.form)
      // this.form.type === 1 && (this.dlgVisible = true)
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
